import React from "react";
import ListItem from "@material-ui/core/ListItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";
import ListSubheader from "@material-ui/core/ListSubheader";
import DashboardIcon from "@material-ui/icons/Dashboard";
import ShoppingCartIcon from "@material-ui/icons/ShoppingCart";
import AssignmentIcon from "@material-ui/icons/Assignment";
import { Divider, Link } from "@material-ui/core";

function listItems(onClose) {
  return (
    <div>
      {mainListItems(onClose)}
      <Divider />
      {secondaryListItems(onClose)}
    </div>
  );
}

export default listItems;

function RenderListItem({ item, onClose }) {
  return (
    <Link href={item.to} color="inherit">
      <div onClick={onClose}>
        <ListItem button>
          <ListItemIcon>{item.icon}</ListItemIcon>
          <ListItemText color="inherit" primary={item.title} />
        </ListItem>
      </div>
    </Link>
  );
}

function mainListItems(onClose) {
  const list = [
    {
      title: "Home",
      icon: <AssignmentIcon />,
      to: "/#/",
    },
    {
      title: "Project",
      icon: <AssignmentIcon />,
      to: "/#/project",
    },
  ];

  return list.map((item, index) => (
    <RenderListItem key={index} item={item} onClose={onClose} />
  ));
}

function secondaryListItems(onClose) {
  const list = [
    {
      title: "Clock",
      icon: <DashboardIcon />,
      to: "/#/clock",
    },
    {
      title: "Chatbot",
      icon: <ShoppingCartIcon />,
      to: "/#/chatbot",
    },
  ];

  return (
    <div>
      <ListSubheader inset>Examples</ListSubheader>
      {list.map((item, index) => (
        <RenderListItem key={index} item={item} onClose={onClose} />
      ))}
    </div>
  );
}
